@import "vars";
@import "mixins";

#uebungen {
    margin-top: 50px;
}

.uBtn {
    margin: 5px;
    width: 100px;
    height: 50px;
    border: 2px solid black;
    border-radius: 5px;
    cursor: pointer;
}

.uBtn-active {
    background: rgb(126, 122, 122);
}

#imageContainer {
    width: 1000px;
    border: 2px solid black;
    margin: 5px auto;
    background: lightgrey;
    border-radius: 10px;
}

#image {
    width: 900px;
    margin: 50px auto;
}

.prevnext {
    font-size: 50px;
    top: 50%;
    position: absolute;
    width: 30px;
    transform: translate(0, -50%);
    cursor: pointer;
    transition: color 400ms, background 500ms;
    background: none;
    text-align: center;
    &:hover {
        color: rgba(194, 55, 55, 0.7);
        background: rgba(110, 110, 110, 0.849)
    }
}

#prev {
    left: 15px;
}

#next {
    right: 15px;
}
